<template>
	<div class="container">
		<Layout class="layout">
			<Sider
					class="side-bar"
					:style="{height: 'calc(100vh - 35px)', background: '#fff', overflow: 'auto'}"
					ref="side"
					:collapsed-width="78"
			>
				<Menu ref="side_menu" :active-name="activeName" :open-names="['1', '2', '3', '4']" theme="light" width="auto" @on-select="onSelect">
					<Submenu name="1" v-if="isVerified">
						<template slot="title">
							<Icon type="ios-pin"></Icon>
							<span>收货信息</span>
						</template>
						<MenuItem name="myAddress" to="/home/myAddress">我的收货信息</MenuItem>
						<MenuItem name="addAddress" to="/home/addAddress">添加收货信息</MenuItem>
					</Submenu>
					<Submenu name="2" v-if="isVerified">
						<template slot="title">
							<Icon type="md-clipboard"></Icon>
							<span>购物订单</span>
						</template>
						<MenuItem name="myOrder/1" to="/home/myOrder/1">进行中</MenuItem>
						<MenuItem name="myOrder/2" to="/home/myOrder/2">历史订单</MenuItem>
					</Submenu>
					<Submenu name="3" v-if="isVerified">
						<template slot="title">
							<Icon type="ios-cart"></Icon>
							<span>购物车</span>
						</template>
						<MenuItem name="myShoppingCart" to="/home/myShoppingCart">我的购物车</MenuItem>
					</Submenu>
					<Submenu name="4">
						<template slot="title">
							<Icon type="ios-person"></Icon>
							<span>店铺资料</span>
						</template>
						<MenuItem name="info" to="/home/info">基本信息</MenuItem>
						<MenuItem name="myScan" to="/home/myScan">审核资料</MenuItem>
					</Submenu>
				</Menu>
			</Sider>
			<Layout class="layout">
				<Header :style="{background: '#fff'}">
					<h2>{{homeTitle}}</h2>
				</Header>
				<Content class="content">
					<transition mode="out-in">
						<router-view></router-view>
					</transition>
				</Content>
			</Layout>
		</Layout>
	</div>
</template>

<script>
	import {mapState, mapMutations} from "vuex";

	export default {
		name: "Home",

		data() {
			return {
				activeName: 'myScan',
				isVerified: false,
				bar: {
					myAddress: "收货信息",
					addAddress: "添加收货信息",
					'myOrder/1': "进行中订单",
					'myOrder/2': "历史订单",
					myShoppingCart: "购物车",
					info: "基本信息",
					myScan: "审核资料"
				}
			};
		},

		computed: {
			...mapState(["homeTitle"]),
		},

		beforeRouteUpdate(to, from, next) {
			next();
			this.updateMenu(to);
		},

		mounted() {
			this.updateMenu(this.$route);

			let userInfo = JSON.parse(localStorage.getItem('userInfo'));
			this.isVerified = userInfo.accattach.status === 1;
		},

		methods: {
			...mapMutations(['SET_HOME_TITLE']),

			updateMenu(obj) {
				let tmp = obj.path.split('/');
				let s = (tmp.length > 3) ? `${tmp[2]}/${tmp[3]}` : tmp[2];
				this.SET_HOME_TITLE(this.bar[s]);
				this.activeName = s;
				this.$nextTick(() => {
					this.$refs.side_menu.updateOpened();
					this.$refs.side_menu.updateActiveName();
				});
			},

			onSelect(name) {
				this.SET_HOME_TITLE(this.bar[name]);
			}
		}
	};
</script>

<style scoped>
	.side-bar a {
		color: #232323;
	}

	.user-icon {
		height: 200px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.user-icon span {
		font-size: 96px;
	}

	.user-img {
		margin-bottom: 15px;
		width: 96px;
		height: 96px;
		border-radius: 48px;
		overflow: hidden;
	}

	.user-img img {
		width: 100%;
	}

	.content {
		margin: 15px;
		background-color: #fff;
		padding: 15px;
	}

	.layout-footer-center {
		padding: 0px 15px;
		padding-bottom: 15px;
		text-align: center;
	}
</style>



